"use client";
import { useState } from "react";
import {
  Box,
  Card,
  Checkbox,
  List,
  ListItem,
  ListItemButton,
  ListItemText,
  Typography,
  Divider,
  Switch,
} from "@mui/material";

const rolesData = ["Admin", "Client", "Superuser", "Viewer", "Auditor"];

const menuData = [
  { id: 1, name: "Dashboard" },
  { id: 2, name: "Vehicles" },
  { id: 3, name: "Drivers" },
  { id: 4, name: "Location Master" },
  { id: 5, name: "Client Setup" },
  { id: 6, name: "Configurations" },
];

export default function MenuProfile() {
  const [selectedRole, setSelectedRole] = useState("Admin");

  // State for role-based access mapping
  const [accessMap, setAccessMap] = useState({
    Admin: { 1: true, 2: true, 3: true, 4: true, 5: true, 6: true },
    Client: { 1: true, 2: false, 3: false, 4: false, 5: true, 6: false },
    Superuser: { 1: true, 2: true, 3: true, 4: true, 5: true, 6: true },
    Viewer: { 1: true, 2: false, 3: false, 4: false, 5: false, 6: false },
    Auditor: { 1: true, 2: true, 3: true, 4: false, 5: false, 6: false },
  });

  const handleToggleAccess = (menuId) => {
    setAccessMap((prev) => ({
      ...prev,
      [selectedRole]: {
        ...prev[selectedRole],
        [menuId]: !prev[selectedRole][menuId],
      },
    }));
  };

  return (
    <Box className="flex gap-4 w-full px-2 py-4">
      {/* Roles Panel */}
      <Card className="w-1/4 shadow-md border">
        <Typography className="p-3 font-semibold text-gray-700">
          Roles
        </Typography>
        <Divider />
        <List>
          {rolesData.map((role) => (
            <ListItem key={role} disablePadding>
              <ListItemButton
                selected={selectedRole === role}
                onClick={() => setSelectedRole(role)}
                className="hover:bg-gray-100"
              >
                <ListItemText
                  primary={role}
                  primaryTypographyProps={{
                    className: "text-sm font-medium",
                  }}
                />
              </ListItemButton>
            </ListItem>
          ))}
        </List>
      </Card>

      {/* Menus Access Panel */}
      <Card className="w-3/4 shadow-md border">
        <Typography className="p-3 font-semibold text-gray-700">
          Menu Access for: <span className="text-blue-600">{selectedRole}</span>
        </Typography>
        <Divider />

        <List>
          {menuData.map((menu) => (
            <ListItem
              key={menu.id}
              secondaryAction={
                <Switch
                  checked={accessMap[selectedRole][menu.id]}
                  onChange={() => handleToggleAccess(menu.id)}
                />
              }
            >
              <ListItemText
                primary={menu.name}
                primaryTypographyProps={{ className: "text-sm font-medium" }}
              />
            </ListItem>
          ))}
        </List>
      </Card>
    </Box>
  );
}
